@import "../../../../node_modules/bootstrap/scss/_functions.scss";
@import "../../../../node_modules/bootstrap/scss/_variables.scss";
@import "../page/vendor/bootstrap/_variables.scss";
@import "../../../../node_modules/bootstrap/scss/mixins/_breakpoints.scss";
@import "../../../../node_modules/bootstrap/scss/mixins/_grid.scss";
@import "../../../../node_modules/bootstrap/scss/mixins/_lists.scss";
@import "../page/base/_variables.scss";
@import "../page/base/_mixins.scss";
.about {
  position: relative;
  display: block;

  &__title {
    @include visually-hidden();
  }

  &.swiper {
    $swiper: &;

    &-container-horizontal > .swiper-pagination-bullets,
    &-pagination-custom,
    &-pagination-fraction {
      bottom: 37px;

      @include media-breakpoint-down(md) {
        bottom: 12px;
      }

      @include media-breakpoint-down(sm) {
        bottom: 7px;
      }

      .swiper-pagination-bullet {
        background-color: $body-color;
        opacity: 1;

        &-active {
          border: 1px solid $body-color;
          background-color: $body-bg;
        }

        @include media-breakpoint-down(sm) {
          width: 6px;
          height: 6px;
        }
      }
    }
  }
}
